define('ember-grid/eg-column/model', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Object.extend({
		init: function init() {
			this.setProperties({
				width: 0,
				resizable: true,
				align: 'left',
				_zones: {}
			});

			this.set('_zones.header', null);
			this.set('_zones.body', null);
			this.set('_zones.footer', null);
		}
	});

});