define('ember-grid/components/eg-render/eg-header/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-header/template'], function (exports, Ember, layout) {

  'use strict';

  // component eg-render/eg-header

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['header'],
    attributeBindings: ['style'],
    classNameBindings: ['isDragging:dragging'],

    isDragging: Ember['default'].computed('draggingHeaderCell', function () {
      return this.get('draggingHeaderCell') !== null;
    }),

    draggingHeaderCell: null,

    style: Ember['default'].computed('width', function () {
      return Ember['default'].String.htmlSafe('width:' + this.get('width') + 'px;');
    }),

    mouseUp: function mouseUp() {
      this.set('draggingHeaderCell', null);
    },

    mouseLeave: function mouseLeave() {
      this.set('draggingHeaderCell', null);
    },

    mouseMove: function mouseMove(event) {
      if (!this.get('isDragging')) {
        return;
      }

      var cell = this.get('draggingHeaderCell');
      var offset = cell.$().offset();
      var newWidth = event.pageX - offset.left;
      if (newWidth < 5) {
        newWidth = 5;
      }

      cell.set('width', newWidth);
    }

  });

});