define('ember-grid/components/eg-render/eg-header-resize-handle/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-header-resize-handle/template'], function (exports, Ember, layout) {

  'use strict';

  // component eg-render/eg-header-resize-handle

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['resize-handle'],
    classNameBindings: ['isDragging:dragging'],

    isDragging: Ember['default'].computed.alias('parentView.isDragging'),

    mouseDown: function mouseDown(event) {
      this.get('parentView').startDragging();
      event.preventDefault();
    }
  });

});