define('ember-grid/components/eg-footer/component', ['exports', 'ember', 'ember-grid/components/eg-column/component', 'ember-grid/components/eg-footer/template'], function (exports, Ember, EmberGridColumn, layout) {

	'use strict';

	// component eg-footer

	exports['default'] = Ember['default'].Component.extend({
			layout: layout['default'],
			classNames: ['footer-def'],

			_column: Ember['default'].computed.alias('parentView._column'),

			willInsertElement: function willInsertElement() {
					this._super.apply(this, arguments);

					Ember['default'].run.scheduleOnce('afterRender', this, function () {
							var parentView = this.get('parentView');
							if (parentView instanceof EmberGridColumn['default']) {
									console.log("FOOTER", this.get('_column').key);
									parentView.set('_column._zones.footer', this.attrs);
									parentView.set('_column._zones.footer.element', this.get('element'));
							}
							this.get('element').style.display = 'none';
					});
			}

	});

});