define('ember-grid/components/eg-render/eg-body-cell/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-body-cell/template'], function (exports, Ember, layout) {

  'use strict';

  // component eg-render/eg-body-cell

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['cell', 'eg-body-cell'],
    classNameBindings: ['isOddRow:odd'],
    attributeBindings: ['style', 'rowIndex:data-row-index'],

    _source: Ember['default'].computed.alias('_column._zones.body.source'),
    _data: Ember['default'].computed.alias('_column._zones.body.data'),

    width: Ember['default'].computed.alias('_column.width'),
    align: Ember['default'].computed.alias('_column.align'),

    isOddRow: Ember['default'].computed('rowIndex', function () {
      return this.get('rowIndex') % 2 === 1;
    }),

    style: Ember['default'].computed('width', 'rowHeight', function () {
      var _getProperties = this.getProperties('width', 'rowHeight');

      var width = _getProperties.width;
      var rowHeight = _getProperties.rowHeight;

      return Ember['default'].String.htmlSafe('display:inline-block; width:' + width + 'px; height:' + rowHeight + 'px');
    })

  });

});