define('ember-grid/components/eg-render/eg-footer-cell/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-footer-cell/template', 'ember-grid/utils/dom-util'], function (exports, Ember, layout, dom_util) {

  'use strict';

  // component eg-render/eg-footer-cell

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],

    classNames: ['cell'],
    attributeBindings: ['style'],

    _footer: Ember['default'].computed.alias('_column._zones.footer'),
    align: Ember['default'].computed.alias('_column.align'),
    width: Ember['default'].computed.alias('_column.width'),

    didInsertElement: function didInsertElement() {
      this._super();
      Ember['default'].run.scheduleOnce('afterRender', this, function () {
        this.renderFooter();
      });
    },

    didReceiveAttrs: function didReceiveAttrs() {
      this._super();
      this.set('_column', this.attrs.column.value);
    },

    renderFooter: function renderFooter() {
      var footer = this.get('_footer');
      if (footer) {
        var sourceElement = footer.element;
        var destinationElement = this.get('element');
        dom_util.moveChildren(sourceElement, destinationElement);
      }
    },

    text: Ember['default'].computed('_column.footer', function () {
      var result = this.get('_column.footer');
      if (result) {
        return result;
      }
      return '';
    }),

    style: Ember['default'].computed('width', function () {
      return 'width:' + this.get('width') + 'px;';
    })
  });

});