define('ember-grid/helpers/eg-add', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports.egAdd = egAdd;

  function egAdd(params /*, hash*/) {
    // convert sum to string to avoid falsy return
    return params[0] + params[1] + '';
  }

  exports['default'] = Ember['default'].Helper.helper(egAdd);

});