define('ember-grid/components/eg-render/eg-footer/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-footer/template'], function (exports, Ember, layout) {

  'use strict';

  // component eg-render/eg-footer

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['footer'],
    attributeBindings: ['style'],

    style: Ember['default'].computed('width', function () {
      return Ember['default'].String.htmlSafe('width:' + this.get('width') + 'px;');
    })
  });

});