define('ember-grid/utils/dom-util', ['exports'], function (exports) {

  'use strict';

  exports.moveChildren = moveChildren;
  exports.removeChildren = removeChildren;
  exports.copyChildren = copyChildren;

  function moveChildren(source, target, replace) {
    if (source == null || target == null) {
      return;
    }
    if (replace) {
      removeChildren(target);
    }
    while (source.firstChild) {
      target.appendChild(source.childNodes[0]);
    }
  }

  function removeChildren(source) {
    if (source == null) {
      return;
    }
    while (source.lastChild) {
      source.removeChild(source.lastChild);
    }
  }

  function copyChildren(source, target, replace) {
    if (source == null || target == null) {
      return;
    }
    if (replace) {
      removeChildren(target);
    }
    if (source.firstChild == null) {
      return;
    }
    for (var i = 0; i < source.childNodes.length; i++) {
      var node = source.childNodes[i].cloneNode(true);
      target.appendChild(node);
    }
  }

});