define('ember-grid/components/ember-grid/component', ['exports', 'ember', 'ember-grid/components/ember-grid/template'], function (exports, Ember, layout) {

  'use strict';

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['ember-grid'],
    attributeBindings: ['style'],

    showHeader: true,
    showFooter: false,

    columns: null,

    style: Ember['default'].computed('bodyWidth', function () {
      return Ember['default'].String.htmlSafe('width:' + this.get('bodyWidth') + 'px;');
    }),

    bodyWidth: Ember['default'].computed.alias('width'),
    contentWidth: Ember['default'].computed('columns.[]', function () {
      var columns = this.get('columns');
      if (columns == null || columns.length === 0) {
        return 0;
      }
      return columns.slice(-1)[0].offset;
    }),
    init: function init() {
      this._super();
      this.columns = new Ember['default'].A([]);
    },

    didReceiveAttrs: function didReceiveAttrs() {
      this._super();
      if (this.columns == null) {
        this.columns = new Ember['default'].A();
      }
      this.height = this.getAttr('height') | 0;
      this.width = this.getAttr('width') | 0;
      this.rowHeight = this.getAttr('rowHeight') | 0;
      this.headerHeight = this.getAttr('headerHeight') | 0;
      this.footerHeight = this.getAttr('footerHeight') | 0;
      this.bodyHeight = this.height - this.headerHeight - this.footerHeight;
      this.data = this.getAttr('data') || Ember['default'].A([]);
      var self = this;
      var offset = 0;
      this.columns.forEach(function (column) {
        offset = self._refreshColumn(column, offset);
      });
    },
    _refreshColumn: function _refreshColumn(column, offset) {
      var body;
      Ember['default'].set(column, 'offset', offset);
      offset += column.width | 0;
      if (column._zones == null) {
        Ember['default'].set(column, '_zones', Ember['default'].Object.create({}));
      }
      if (column._zones.body == null) {
        body = Ember['default'].Object.create({});
        Ember['default'].set(column._zones, 'body', body);
      } else {
        body = column._zones.body;
      }
      var height = this.get('bodyHeight'),
          limit = Math.ceil(height / this.rowHeight) + 10;
      body.setProperties({
        height: height,
        width: column.width,
        rowHeight: this.rowHeight,
        data: this.data,
        offset: 0,
        limit: limit
      });
      return offset;
    },

    _addColumn: function _addColumn(column) {
      var lastColumn = this.columns.slice(-1)[0];
      var lastOffset = 0;
      if (lastColumn != null) {
        lastOffset = lastColumn.offset + (lastColumn.width | 0);
      }
      this.columns.pushObject(column);
      this._refreshColumn(column, lastOffset);
    }
  });

});