define('ember-grid/components/eg-render/eg-body/component', ['exports', 'ember', 'ember-grid/components/eg-render/eg-body/template'], function (exports, Ember, layout) {

  'use strict';

  // componnent eg-render/eg-body

  exports['default'] = Ember['default'].Component.extend({
    layout: layout['default'],
    classNames: ['body'],

    sourceBodies: Ember['default'].computed('columns.@each._zones.body', function () {
      return this.get('columns').map(function (col) {
        return Ember['default'].get(col, '_zones.body');
      });
    }),
    requiredPresent: Ember['default'].computed('attrs.data', 'attrs.columns', 'attrs.height', 'attrs.width', 'attrs.contentWidth', 'attrs.rowHeight', function () {
      if (this.getAttr('data') == null) {
        return false;
      }
      if (this.getAttr('columns') == null) {
        return false;
      }
      if (this.getAttr('height') == null) {
        return false;
      }
      if (this.getAttr('width') == null) {
        return false;
      }
      if (this.getAttr('contentWidth') == null) {
        return false;
      }
      if (this.getAttr('rowHeight') == null) {
        return false;
      }
      return true;
    }),
    actions: {
      scrollSource: function scrollSource(offset, limit) {
        var bodies = this.get('sourceBodies') || [];
        Ember['default'].run.debounce(function () {
          bodies.map(function (body) {
            if (body != null) {
              Ember['default'].set(body, 'offset', Math.max(offset - 60, 0));
              Ember['default'].set(body, 'limit', limit + 60);
            }
          });
        }, 100);
      }
    }
  });

});